import axios from 'axios';

export const GET_NEWS_REQUEST = "GET_NEWS_REQUEST";
export const GET_NEWS_SUCCESS = "GET_NEWS_SUCCESS";
export const GET_NEWS_FAIL = "GET_NEWS_FAIL";

export const getNews = () => {
    return dispatch => {
        dispatch(addPhotosRequest());

        let one = "https://content.guardianapis.com/search?q=news&api-key=e0ad32c0-9fb7-439f-8cbf-41fdb32f22d3"
        let two = "https://api.pexels.com/v1/search?query=news&per_page=10"

        const requestOne = axios.get(one);
        const requestTwo = axios({
            method: 'get',
            url:two,
            headers:{Authorization:"563492ad6f9170000100000190b900612c0e41a281be655afc75973d"}
        });

        axios.all([requestOne, requestTwo]).then(axios.spread((...responses) => {
            dispatch(addPhotosSuccess(responses))
        }))
        .catch(err => {
            dispatch(addPhotosError(err))
        })
    }


}

const addPhotosRequest = () => {
    return {
        type: 'GET_NEWS_REQUEST',
    }
};

const addPhotosSuccess = (news) => {
    return {
        type: 'GET_NEWS_SUCCESS',
        payload: news
    }
};

const addPhotosError = (err) => {
    return {
        type: 'GET_NEWS_FAIL',
        payload: err
    }
};


